<template>
    <div id="map-sz"></div>
</template>

<script>
    import _ from 'lodash'
    import echarts from 'echarts/lib/echarts';
    import 'echarts/lib/component/geo'
    import 'echarts/map/js/world.js';

    import sz from '@/data/sz';
    import ComJSON from '@/data/community';
    import OutPJSON from '@/data/outpatient'

    export default {
        name: 'SzMap',
        methods: {
            initMap() {
                echarts.registerMap('shenzhen', sz);
                const communityData = this.initCommunity();
                const outpatientData = this.initOutpatient()
                const option = {
                    // backgroundColor: '#091c3d',
                    visualMap: {
                        show: false,
                        min: 0,
                        max: 100,
                        seriesIndex: 0,
                        inRange: {
                            color: ['#eac736', 'red']
                        },
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    geo: {
                        show: true,
                        map: 'shenzhen',
                        zoom: 1.2,
                        label: {
                            normal: {
                                show: false
                            },
                            emphasis: {
                                show: false,
                            }
                        },
                        roam: true,
                        itemStyle: {
                            normal: {
                                areaColor: 'transparent',
                                borderColor: '#3fdaff',
                                borderWidth: 2,
                                shadowColor: 'rgba(63, 218, 255, 0.5)',
                                shadowBlur: 30
                            },
                            emphasis: {
                                areaColor: '#2B91B7',
                            }
                        },
                    },
                    series: [
                        {
                            name: '',
                            type: 'heatmap',
                            coordinateSystem: 'geo',
                            pointSize: 3,
                            blurSize: 4,
                            data: communityData,
                            z: 10,
                        },
                        {
                            name: 'outpatient',
                            type: 'scatter',
                            coordinateSystem: 'geo',
                            data: outpatientData,
                            symbolSize: function () {
                                return 12;
                            },
                            symbol: 'image://data:image/svg+xml;base64,',
                            label: {
                                normal: {
                                    formatter: '{b}',
                                    position: 'right',
                                    show: false,
                                    color: '#37A2DA',
                                },
                                emphasis: {
                                    show: true
                                }
                            },
                        }
                    ]
                };
                const myChart = echarts.init(document.getElementById('map-sz'), 'light');
                myChart.setOption(option);
            },
            initCommunity(){
                return _.chain(ComJSON)
                    .map(d => ({
                        name: d.name,
                        value: d.coord.concat(100),
                    }))
                    .value()
            },
            initOutpatient() {
                return OutPJSON.map(d => ({
                    name: d.ORGAN_NAME,
                    value: [d.latitude1, d.longitude1, 100]
                }))
            }
        },
        mounted() {
            this.initMap();
        }
    }
</script>

<style lang="less" scoped>
    #map-sz{
        width: 80%;
    }
</style>